import {
    LIKE_POST,
    UNLIKE_POST
} from '../types'
import axios from 'axios'
import store from '../store'

export const likePost = (postId) => (dispatch) => {
    let posts = store.getState().data.posts;
    let index = posts.map(item => item._id).indexOf(postId);
    posts[index].likedByUser = true;
	posts[index].likeCount += 1;
    dispatch({
        type: LIKE_POST,
        payload: posts
    })
    axios
        .post(`/api/post/${postId}/likes`)
        .then(res => {
        })
}

export const unlikePost = (postId) => (dispatch) => {
    let posts = store.getState().data.posts;
    let index = posts.map(item => item._id).indexOf(postId);
    posts[index].likedByUser = false;
	posts[index].likeCount -= 1;
    dispatch({
        type: UNLIKE_POST,
        payload: posts
    })
    axios
        .delete(`/api/post/${postId}/likes`)
        .then(res => {
        }) 
}