import React from 'react';
import { Paper, Typography, CircularProgress } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles' 

import { connect } from 'react-redux'
import { verify } from '../../redux/actions/userActions'

const styles = {
    paper: {
        padding : "25px 25px 75px 25px",
        "position": "relative",
        "left": "50%",
        "margin-top": "50px",
        "transform": "translate(-50%, 0%)",
		"display": "table"
    },
    error: {
        color: "red",
        "font-weight": "bold",
		"margin-bottom": "25px"
    },
    success: {
        color: "green",
        "font-weight": "bold",
		"margin-bottom": "25px"
    },
	progress: {
		"position": "relative",
        "left": "50%",
        "margin-top": "200px",
        "transform": "translate(-50%, 0%)",
	}
}
class Verify extends React.Component{
    componentDidMount() {
        const { id } = this.props.match.params
        this.props.verify(id)
    }
    render() {
        const { classes, UI: { loading, errors, message: { message } } } = this.props
        return(
            (loading) ? (
                <CircularProgress size={100} className = {classes.progress} />
            ) : (
                (errors.message) ? (
                    <Paper elevation = {6} className = {classes.paper}>
                        <Typography variant = "h5" className = {classes.error}>Error</Typography>
                        <Typography variant = "subtitle1">{errors.message}</Typography>
                    </Paper>
                ) : (
                    <Paper elevation = {6} className = {classes.paper}>
                        <Typography variant = "h5" className = {classes.success}>{message}</Typography>
                        <Typography variant = "subtitle1">Now you can login and connect with other people</Typography>
                    </Paper>
                )
            )
        )
    }
}

const mapStateToProps = (state) => ({
    UI: state.UI
})

const mapActionsToProps = {
    verify
}

export default connect(
    mapStateToProps,
    mapActionsToProps,
)(withStyles(styles)(Verify))