import React from 'react';
import { withStyles } from '@material-ui/core/styles'
import { Button, Paper, TextField, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@material-ui/core/';
import {
  Typography,
  ButtonBase,
  Avatar
} from '@material-ui/core';

import CircularProgress from '@material-ui/core/CircularProgress';
import SnackBar from './snackBar'
import PostEditor from '../PostEditor'

import { connect } from 'react-redux'
import { uploadPost } from '../../redux/actions/dataActions'

const styles = {
    container: {
      width: "600px",
      "position": "relative",
      "left": "50%",
      "top": "50%",
      "transform": "translate(-50%, 0%)",
      "background-color": "white",
      "box-shadow": "0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12)",
      transition: "0.3s",
      "padding": "20px 0px 20px 20px",
      "justify-content": "flex-start",
      '&:hover': {
        "background-color": "#d9d9d9"
      }
    },
    avatar: {
      "background-color": "rgb(0, 188, 212)",
      "margin-right": "15px"
    },
    placeHolder: {
      opacity: 0.4
    },
}

class PostWriter extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            open: false,
            snackBarOpen: false
        }
    }
    componentWillReceiveProps(nextProps) {
      if(nextProps.UI.loading === false && this.props.UI.loading === true) {
        this.closeDialog()
        this.setState({snackBarOpen: true})
      }
    }
    openDialog = () => {
        this.setState({open: true})
    }
    closeDialog = () => {
        this.setState({open: false})
    }
    closeSnackBar = () => {
        this.setState({snackBarOpen: false})
    }
    handleUpload = (postContent) => {
      const newPost = {
        content: postContent
      }
      this.props.uploadPost(newPost)
    }
    render() {
        const { classes, UI: { loading, errors, message: { message } } } = this.props;
        return(
            <div>
            <ButtonBase className = {classes.container} onClick = {this.openDialog}>
              <Avatar className = {classes.avatar}>
                {JSON.parse(localStorage.user).username[0].toUpperCase()}
              </Avatar>
              <Typography className = {classes.placeHolder}>What are you thinking</Typography>
            </ButtonBase>
            <PostEditor 
              loading = {loading} 
              open = {this.state.open}
              closeDialog = {this.closeDialog}
              handleUpload = {this.handleUpload}
              mode = "Post"
            />
            <SnackBar
                  errors = {errors} 
                  message = {message} 
                  open = {this.state.snackBarOpen}
                  handleClose = {this.closeSnackBar}
             />
          </div>
        )
    }
}

const mapStateToProps = (state) => ({
  UI: state.UI
})

const mapActionsToProps = {
  uploadPost
}

export default connect(
  mapStateToProps,
  mapActionsToProps,
)(withStyles(styles)(PostWriter))
