import {
    LOADING_UI,
    STOP_LOADING_UI,
    LOADING_DATA,
    UPLOAD_POST,
    SET_MANY_POSTS,
    SET_POST,
    EDIT_POST,
    DELETE_POST,
    SET_DATA_ERROR,
    SET_RESPONSE_MESSAGE,

} from '../types'
import axios from 'axios'
import store from '../store'

export const getPosts = () => (dispatch) => {
    dispatch({ type: LOADING_DATA })
    axios.get('/api/posts', {
        params: {
            lastDate: new Date(),
            limit: 10
        }
    }). then(res => {
            let userId = localStorage.userId
            dispatch({
                type: SET_MANY_POSTS,
                payload: res.data
            })
        })
        .catch(err => {
            // console.log(err)
            dispatch({
                type: SET_DATA_ERROR,
                payload: err.response.data
            })
        })
}

export const getPost = (postId) => (dispatch) => {
    dispatch({
        type: SET_POST
    })
}

export const uploadPost = (post) => (dispatch) => {
    dispatch({ type: LOADING_UI})
    axios.post('/api/newpost', post)
         .then(res => {
             dispatch({
                 type: UPLOAD_POST,
                 payload: res.data.post
             })
             dispatch({
                 type: SET_RESPONSE_MESSAGE,
                 payload: res.data
             })
         })
         .catch(err => {
             dispatch({
                 type: SET_DATA_ERROR,
                 payload: err.response.data
             })
         }) 
}

export const editPost = (postId, newPost, callback) => (dispatch) => {
    dispatch({ type: LOADING_UI})
    axios
        .put(`/api/post/${postId}`, newPost)
        .then(res => {
            let posts = store.getState().data.posts;
            let index = posts.map(item => item._id).indexOf(postId);
            posts[index] = res.data.post;
            dispatch({
                type: EDIT_POST,
                payload: posts
            })
            dispatch({
                type: STOP_LOADING_UI
            })
            callback()
        })
} 
export const deletePost = (postId) => (dispatch) => {
    let posts = store.getState().data.posts;
    let index = posts.map(item => item._id).indexOf(postId);
    posts.splice(index, 1);
    dispatch({
        type: DELETE_POST,
        payload: posts
    })
    axios
        .delete(`/api/post/${postId}`)
        .then(res => {

        })
}