import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Header from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Avatar } from '@material-ui/core'
import Options from '../Options' 

import { connect } from 'react-redux'
import { logout } from '../../redux/actions/userActions'
import { toggleSideNav } from '../../redux/actions/uiActions'

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    "margin-bottom": "25px"
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  avatar: {
    "background-color": "rgb(0, 188, 212)",
  },
});

class AppBar extends React.Component {
  render(){
    const { classes, toggleSideNav} = this.props;
    const userOptions = [
      {
        text: "Log out",
        action: this.props.logout
      },
    ]
    return (
        <div className={classes.root}>
          <Header position="static">
            <Toolbar>
              <IconButton edge="start" className={classes.menuButton} color="inherit" onClick = {toggleSideNav}>
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" className={classes.title}>
                News
              </Typography>
              <Options optionsList = {userOptions} displayComponent = {
                <Avatar className = {classes.avatar}>
                  {JSON.parse(localStorage.user).username[0].toUpperCase()}
                </Avatar>
              }/>
            </Toolbar>
          </Header>
        </div>
      );
  }
}

const mapActionsToProps = {
    logout,
    toggleSideNav
}

export default connect(
    null,
    mapActionsToProps
)(withStyles(styles)(AppBar))
