import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Login from './pages/Login/login'
import Signup from './pages/Signup/signup'
import Dashboard from './pages/Dashboard/dashboard'
import Profile from './pages/Profile/profile'
import Verify from './pages/Verify/verify'
import AuthRoute from './util/AuthRoute'
import './index.css'

import { Provider } from 'react-redux';
import { SET_AUTHENTICATED, SET_USER_DATA } from './redux/types';
import store from './redux/store'

import axios from 'axios';

const token = localStorage.JWTToken;
if(token) {
  store.dispatch({type: SET_AUTHENTICATED})
  store.dispatch({
    type: SET_USER_DATA,
    payload: JSON.parse(localStorage.user)
  })
  axios.defaults.headers.common['Authorization'] = token;
}

function App() {
  return (
    <Provider store = {store}>
      <Router>
        <Switch>
          <Route exact path = "/login" component = {Login}></Route>
          <Route exact path = "/signup" component = {Signup}></Route>
          <Route exact path = "/verify/:id" component = {Verify}></Route>
          <AuthRoute exact path = "/" component = {Dashboard}></AuthRoute>
          <AuthRoute exact path = "/profile" component = {Profile}></AuthRoute>
        </Switch>
      </Router>
    </Provider>
  );
}

export default App;
