import React from 'react';
import { MenuList, MenuItem, IconButton, Popper, Paper } from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert'

class Options extends React.Component {
   constructor() {
     super()
     this.state = {
       anchorEl: null
     }
   }
   toggleMenu = (e) => {
    if(!this.state.anchorEl) {
      this.setState({anchorEl: e.currentTarget})
    }
    else {
      this.setState({anchorEl: null})
    }
   }
    render() {
        const { optionsList, displayComponent } = this.props; 
        return (
            <div>
              <IconButton onClick={this.toggleMenu}>
                {displayComponent}
              </IconButton>
              <Popper open = {Boolean(this.state.anchorEl)} anchorEl ={this.state.anchorEl}>
                <Paper elevation = {2}>
                  <MenuList
                    id="simple-menu"
                    autoFocusItem = {Boolean(this.state.anchorEl)}
                  >
                      {optionsList.map((option) => (
                        <MenuItem key = {option.text} onClick = {option.action}>{option.text}</MenuItem>
                      ))}
                  </MenuList>
                </Paper>
              </Popper>
            </div>
        );
    }
}

export default Options;