import {
	SET_COMMENT,
    ADD_COMMENT,
    EDIT_COMMENT,
    DELETE_COMMENT
} from '../types'
import axios from 'axios'
import store from '../store'

 export const getComments = (postId, callback) => (dispatch) => {
    axios
        .get(`/api/post/${postId}/comments`)
        .then(res => {
			let posts = store.getState().data.posts;
			let index = posts.map(item => item._id).indexOf(postId)
			posts[index].comments = res.data
			dispatch({
				type: SET_COMMENT,
				payload: posts
			})
			callback()
        })
 }

 export const addComment = (postId, comment) => (dispatch) => {
	axios
		.post(`/api/post/${postId}/comments`,comment)
		.then(res => {
			let posts = store.getState().data.posts;
			let index = posts.map(item => item._id).indexOf(postId)
			posts[index].comments.unshift(res.data.comment)
			posts[index].commentCount += 1
			dispatch({
				type: ADD_COMMENT,
				payload: posts
			})
		}) 
 }
 
 export const editComment = (postId, commentId, newComment) => (dispatch) => {
	let posts = store.getState().data.posts;
	let index = posts.map(item => item._id).indexOf(postId)
	let commentIndex = posts[index].comments.map(item => item._id).indexOf(commentId);
	posts[index].comments[commentIndex].content = newComment.content;
	dispatch({
		type: EDIT_COMMENT,
		payload: posts
	})
 	axios
 		.put(`/api/post/${postId}/comments`, newComment, {
 			params: {
 				id: commentId
 			}
 		})
 		.then(res => {
 		})
 }

 export const deleteComment = (postId, commentId) => (dispatch) => {
	let posts = store.getState().data.posts;
	let index = posts.map(item => item._id).indexOf(postId);
	let commentIndex = posts[index].comments.map(item => item._id).indexOf(commentId);
	posts[index].comments.splice(commentIndex, 1);
	posts[index].commentCount -= 1;
	dispatch({
		type: DELETE_COMMENT,
		payload: posts
	})
	axios
		.delete(`/api/post/${postId}/comments`, {
			params: {
				id: commentId
			}
		})
		.then(res => {
			
		})
 }