import {
    SET_AUTHENTICATED,
    SET_USER_DATA,
    SET_UNAUTHENTICATED,
} from '../types'

const initialState = {
    authenticated: false,
    user: {}
}

export default function(state = initialState, action) {
    switch(action.type) {
        case SET_AUTHENTICATED:
            return {
                ...state,
                authenticated: true
            }
        case SET_USER_DATA:
            return {
                ...state,
                user: action.payload
            }
        case SET_UNAUTHENTICATED:
            return {
                ...state,
                authenticated: false
            }
        default: 
            return state
    }
}