import React from 'react'
import { TextField, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Avatar, Button, CircularProgress } from '@material-ui/core'
import { withStyles } from '@material-ui/core/styles'

import PostOptions from './postOptions'

const styles = {
    postField: {
        "position": "relative",
        "left": "50%",
        "top": "50%",
        "transform": "translate(-50%, 0%)",
        width: "300px"
    },
    avatar: {
      "background-color": "rgb(0, 188, 212)",
      "margin-right": "15px"
    },
    dialog: {
      width: "400px",
      height: "300px"
    },
    fieldContainer: {
      "overflow-y": "hidden"
    },
    postHeader: {
      display: "flex",
      "justify-content": "space-between",
    },
    subHeader: {
      display: "flex",
      "align-items": "center"
    }
}

class PostEditor extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      postContent: ""
    }
  }
  handleChange = (e) => {
    this.setState({postContent: e.target.value})
  }
	render() {
		const { classes, loading, open, closeDialog, handleUpload, mode } = this.props;
		return (
			<Dialog open={open} onClose={closeDialog}>
              <div className = {classes.dialog}>
                <DialogTitle id="form-dialog-title">
                  <div className = {classes.postHeader}>
                    <div className = {classes.subHeader}>
                      <Avatar className = {classes.avatar}>
            			{JSON.parse(localStorage.user).username[0].toUpperCase()}
            		  </Avatar>
                      <span>
                        {JSON.parse(localStorage.user).username}
                      </span>
                    </div>
                    <PostOptions/>
                  </div>
                </DialogTitle>
                <DialogContent className = {classes.fieldContainer}>
                  <TextField
                      autoFocus
                      className = {classes.postField}
                      margin="dense"
                      placeholder = "What are you thinking"
                      multiline
                      rows = {2}
                      rowsMax = {4}
                      onChange = {this.handleChange}
                  />
                </DialogContent>
                <DialogActions>
                  <Button onClick={closeDialog} color="primary" disabled = {loading}>
                    Cancel
                  </Button>
                  <Button 
                    onClick={() => handleUpload(this.state.postContent)} 
                    color="primary" 
                    disabled = {loading}
                  >
                    {loading ? (
                          <CircularProgress size={24} />
                      ) : (
                          mode
                      )}
                  </Button>
                </DialogActions>
              </div>
            </Dialog>
			);
	}
}

export default withStyles(styles)(PostEditor)