import {
    LOADING_DATA,
    UPLOAD_POST,
    SET_MANY_POSTS,
    SET_POST,
    SET_DATA_ERROR,
    LIKE_POST,
    UNLIKE_POST,
    EDIT_POST,
    DELETE_POST,
    SET_COMMENT,
    ADD_COMMENT,
    DELETE_COMMENT
} from '../types'

const intialState = {
    posts: [],
    post: [],
    errors: {},
    loading: false
}

export default function (state = intialState, action) {
    switch (action.type) {
        case LOADING_DATA:
            return {
                ...state,
                loading: true
            }
        case UPLOAD_POST:
            return {
                ...state,
                posts: [action.payload, ...state.posts],
            }
        case SET_MANY_POSTS:
            return {
                ...state,
                posts: action.payload,
                loading: false
            }
        case SET_POST:
            return {
                ...state,
                post: action.payload,
                loading: false
            }
        case LIKE_POST:
        case UNLIKE_POST:
        case EDIT_POST:
        case DELETE_POST:
        case SET_COMMENT:
        case ADD_COMMENT:
        case DELETE_COMMENT:
            return {
                ...state,
                posts: action.payload
            }
        case SET_DATA_ERROR:
            return {
                ...state,
                errors: action.payload
            }
        default:
            return state
    }
}