import React from 'react'
import PostWriter from '../../components/PostWriter'
import Navbar from '../../components/Navbar'
import CircularProgress from '@material-ui/core/CircularProgress';
// import Comment from '../../components/Comment'
import { withStyles } from '@material-ui/core/styles'
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime';
import Post from '../../components/Post'

import { connect } from 'react-redux'
import { getPosts } from '../../redux/actions/dataActions'
import { likePost, unlikePost } from '../../redux/actions/likeActions'

const styles = {
    loading: {
        "position": "relative",
        "left": "50%",
        "margin-top": "200px",
        "transform": "translate(-50%, 0%)",
    }
}

class Dashboard extends React.Component{
    componentDidMount() {
        this.props.getPosts()
    }
    render() {
        const { posts, loading } = this.props.data
        const { classes, likePost, unlikePost } = this.props
		dayjs.extend(relativeTime);
        return(
            <div>
                <Navbar/>
                <PostWriter/>
                {loading ? (
                    <CircularProgress size = {100} className = {classes.loading}/>
                ) : (
                    posts.map((post) => 
						<Post key = {post._id} post = {post} likeStatus = {post.likedByUser} comments = {post.comments} commentCount = {post.commentCount} />
						//If I do not extract the likeStatus which is actually the change,
						//the Post Component will not re-render
                    )
                )}
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    data: state.data
})

const mapActionsToProps = {
    getPosts,
	likePost,
    unlikePost
}

export default connect(
    mapStateToProps,
    mapActionsToProps,
)(withStyles(styles)(Dashboard))