import {
    LOADING_UI,
    STOP_LOADING_UI,
    SET_ERROR,
    SET_RESPONSE_MESSAGE,
    TOGGLE_SIDE_NAV
} from '../types'

const intialState = {
    loading: false,
    sideNavOpen: false,
    errors: {},
    message: {}
}

export default function(state = intialState, action) {
    switch(action.type) {
        case LOADING_UI:
            return {
                ...state,
                loading: true,
            }
        case STOP_LOADING_UI: 
            return {
                ...state,
                loading: false
            }
        case TOGGLE_SIDE_NAV: 
            return {
                ...state,
                sideNavOpen: !state.sideNavOpen
            }
        case SET_ERROR:
            return {
                ...state,
                loading: false,
                errors: action.payload
            }
        case SET_RESPONSE_MESSAGE:
            return {
                ...state,
                loading: false,
                message: action.payload
            }
        default: 
            return state
    }
}